html, body {
  box-sizing: border-box;
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.app {
  flex-direction: column;
  flex: 1;
  display: flex;
}

header {
  text-align: center;
  box-sizing: border-box;
  background: #f6f8fa;
  padding: 1rem;
  box-shadow: inset 0 -.125rem #d1d9e0;
}

main {
  flex: 1;
  padding: 0 0 1rem;
}

footer {
  text-align: center;
  background: #fff;
  padding: .5rem;
}

@keyframes float-stars {
  0% {
    transform: translate(0);
  }

  to {
    transform: translate(100px, 300px);
  }
}

.animate-stars {
  opacity: .4;
  will-change: transform;
  animation: 6s linear infinite float-stars;
}

.profile-description {
  opacity: .85;
  background-image: url("https://images.pexels.com/photos/29506610/pexels-photo-29506610.jpeg");
}

.connectionmodalcontainer {
  color: #fff;
  z-index: 9999;
  pointer-events: auto;
  background: #02060ce6;
  border: 1px solid #000;
  border-radius: 8px;
  align-items: center;
  gap: 30px;
  width: auto;
  padding: 15px;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateY(-50%)translate(-50%);
}

.refreshbtton {
  color: #fff;
  border: unset;
  background: unset;
}

.refreshbtton:hover {
  font-weight: 700;
  transform: scale(1.25);
}

.online_status {
  display: flex;
  position: relative;
}

.phantom-card {
  background: linear-gradient(-45deg, #eee 40%, #fafafa 50%, #eee 60%) 100% 0 / 300%;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: box-shadow .3s;
  animation: 1s linear infinite shimmer;
}

@keyframes shimmer {
  to {
    background-position-x: 0%;
  }
}

